.section__wrapper {
  display: flex;
  text-align: left;
  align-items: center;
  padding: 10px;
  min-width: 200px;
  object-fit: contain;
  height: 40px;
  /* margin-left: -10px; */
  margin-bottom: -2px;
}

.section__wrapper:hover,
.section__titlenBadge:hover {
  background-color: whitesmoke;
  cursor: pointer;
  color: black !important;
}

.section__content {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}

.section__content > p {
  font-size: 12px;
  color: #b6b8ba;
  margin-top: 3px;
}

.section__titlenBadge {
  display: flex;
  justify-content: flex-start;
  /* align-items: center; */
}

.section__titlenBadge > p {
  font-size: 14px;
  font-weight: 500;
  color: gray;
}
.section__titlenBadge > h5 {
  margin-left: 5px;
  font-weight: 500;
  font-size: 12px;
}

.section__iconItem {
  font-size: 20px !important;
  color: gray;
  margin-left: 10px;
}

.item--selected {
  border-bottom: 4px solid #0b57d0;

  color: #0b57d0;
}

.item--selected > .section__iconItem,
.item--selected > div > .section__titlenBadge > p {
  color: #0b57d0;
}
