.sidebar {
  padding: 10px;
  background-color: #f6f8fc;
  flex: 0.18;
}

.sidebar__button {
  background-color: #c2e7ff !important;
  padding: 15px 20px !important;
  border-radius: 15px !important;
  margin-bottom: 20px !important;
}

.sidebar__button:hover {
  box-shadow: rgba(49, 49, 49, 0.08) 0px 5px 10px;
}

.sidebar__header > p {
  font-weight: 400;
  margin-left: 10px;
}

.sidebar__compose {
  color: black;
}

.sidebar__bodymenu {
  margin-left: -10px;
}

.sidebar__footerlabels {
  margin-top: 20px;
  margin-left: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 0px 5px 2px;
  margin-right: 25px;
}
.sidebar__footerlabels > h5 {
  font-weight: 400;
  font-size: 17px;
}

.label__icon {
  margin-right: -20px !important;
}

@media (min-width: 320px) and (max-width: 767px) {
  .sidebar {
    display: none;
  }
}

