.email__List {
  overflow-y: scroll;
  width: 100%;
  flex: 0.87;
  padding: 0 10px 10px 0;
  background-color: white;
  border-radius: 15px;
  margin-right: 20px;
}

.email__listsettings {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 999;
  margin-bottom: 5px;
  background-color: white;
  padding: 10px 0 5px 10px;
}

.email__listleft {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.emailHeader__Icons {
  font-size: 18px !important;
  color: black !important;
}

.email__listright {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.email__listright p {
  font-size: small;
}

.email__listSections {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid #eef0f2;
}

/* .email_messageLists {
} */

@media only screen and (max-width: 767px) {
  .email__List {
    margin-right: -60px;
    /* margin-right: -15px; */
  }

  .email__listsettings {
    margin-right: 0px;
    display: flex;
    justify-content: right;
  }

  .email__listright {
    flex: 1;
  }
  .email__listleft {
    display: none;
  }

  #social__tab {
    display: none !important;
  }

  .email__listSections {
    display: none;
  }
}
