.header {
  display: flex;

  justify-content: space-between;
  padding: 8px 15px;
  background-color: #f6f8fc;
}

.header__left {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex: 0.1;
}

.header__left > img {
  object-fit: contain;
  height: 23px;
  margin-left: 8px;
}

.header__right {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex: 0.1;
}

.header__middle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #eaf1fb;
  flex: 0.4;
  padding: 5px 20px;
  border-radius: 10px;
  margin-left: -260px;
}

.input__left {
  display: flex;
  align-items: center;
  width: 90%;
}

.input__left > input {
  width: 100%;
  padding: 8px;
  border: 0;
  background-color: transparent;
  font-size: medium;
  outline-width: 0;
}

@media only screen and (max-width: 767px) {
  .header__middle {
    display: none;
  }
}
