.sidebar__options {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 10px;
  padding: 5px 10px 5px 30px;
}

.sidebar__options > p {
  font-size: 14px;
  color: gray;
  font-weight: 400;
}

.iconand__title {
  display: flex;
  justify-content: center;
  align-items: center;
}

.iconand__title > h5 {
  margin-left: 15px;
  font-weight: 400;
  font-size: 15px;
}

.sidebar__icon {
  font-size: 19px !important;
}

.sidebar__options:hover,
.sidebar__options:hover > p,
.sidebar__options:hover > h5 {
  cursor: pointer;
  background-color: #dddddd;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  font-weight: 800;
}

.sidebar__options--active,
.sidebar__options--active > p,
.sidebar__options--active > div > h5 {
  background-color: #d3e3fd;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  font-weight: 700;
}
