.email__row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  border-bottom: 1px solid #eef0f2;
  height: 40px;
  min-height: 40px;
  max-height: 40px;
}

.email__row:hover {
  width: 100%;
  box-shadow: 1px 1px 3px gray;
}

.email__rowButtons {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex: 0.1;
  padding-left: 20px;
  max-width: 80px;
  min-width: 80px;
}

.row__left > h3 {
  font-size: 14px;
  font-weight: 600;
  /* margin-left: -20px; */
}

.email__rowSubjectDesc {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex: 0.81;
}

.email__rowSubjectDesc > h3 {
  margin-right: 5px;
  font-size: 14px;
  font-weight: 600;
}

.email__rowSubjectDesc > p {
  color: gray;
  font-weight: 400;
  font-size: 14px;
}

.row__iconButton {
  font-size: 20px !important;
  color: lightgray !important;
}

.row__iconButton:nth-child(2) {
  margin-right: 10px;
}

.row__iconButton:nth-child(3) {
  margin-right: 12px;
}

.email__rowtime {
  font-weight: 700;
  font-size: 13px;
  flex: 0.05;
}

.email__rowTitle {
  text-align: left;
  margin-right: 20px;
  margin-left: 10px;
  flex: 0.14;
}

.row__left {
  display: flex;
  justify-content: flex-start;
  flex: 0.95;
  align-items: center;
}

@media (min-width: 320px) and (max-width: 767px) {
  .email__rowtime {
    display: none;
  }

  .email__rowSubjectDesc p {
    display: none;
  }
  .email__rowSubjectDesc h3 {
    font-weight: 400;
    font-size: 14px;
  }

  .email__rowTitle {
    flex: 0.5;
    text-align: center;
  }

  .email__rowSubjectDesc {
    flex: 0.4;
  }

  .star__fav {
    display: none !important;
  }

  .email__rowButtons {
    padding: 0;
  }
}
